export const search_payChecksOnFuture = async (cb) => {
    
    let bdyq2 = {...getStore(qryStore())?.search_future_check};
    bdyq2.params =  {
        businessId:  getStore("profileUser")?.businessId
    }

    
    getStore("searchCFQry").split(" ").map((qry,inDq)=>{
        if(qry){
            bdyq2.params[":search"+inDq] = qry.trim();
        }
    })

    const res = fetchEncryptStream(bdyq2);
    const td = await res;
    if (td) {
        updStore("payChecksOnFutures_list", sortPayChecksOnFutures(convertObj2Array(td),"date"));
        cb && cb();
    }
};




export const get_filter_pay_future = async (cb) => {
    
    updStore("payChecksOnFutures_list", null);
    
    let bdyq2 = {...getStore(qryStore())?.search_future_check};
    bdyq2.params = {
        businessId:  getStore("profileUser")?.businessId,
        ":date1": getStore("date_range_comps")?.initDate - 10,
        ":date2": getStore("date_range_comps")?.lastDate
    }
    getStore("searchCFQry").split(" ").map((qry,inDq)=>{
        if(qry){
            bdyq2.params[":search"+inDq] = qry.trim();
        }
    })
    
    const res = fetchEncryptStream(bdyq2);
    const td = await res;
    //console.log(bdyq2)
    //console.log(td)
    if (td && !td.error) {
        var arr = [];
        
        td &&
          ObjectKeys(td).map((o) => {

            if(
                td[o].date > getStore("date_range_comps")?.initDate - 10 && 
                td[o].date <= getStore("date_range_comps")?.lastDate
            ){

                arr.push(td[o]);
            }
          });
        updStore("payChecksOnFutures_list", sortPayChecksOnFutures(arr,"date"));
    }
   
    cb && cb();
};





export function GroupbyDoc(arr1) {
    var obj = {};
    if (arr1.length > 0) {
      arr1.map((s) => {
        let ddt =  s["date"];
        let keyId = ddt + "_" +  s["comprobanteId"];
        if (!obj[keyId]) {
            obj[keyId] = {};
            let ggg = [
                "date",
                "description",
                "entryName",
                "comprobanteId",
                "payChecksOnFutureId",
                "document"
                
            ]
            ggg.forEach(fld => {
                obj[keyId][fld] = s[fld];
            });
            obj[keyId]["amount"] = 0;
            obj[keyId]["balance"] = 0;
            obj[keyId]["operations"] = [];
        }
        
        if(s["type"]){
            obj[keyId]["balance"] =  parseFloatToDecimal(obj[keyId]["balance"],2)*1 +( s["type"] === "debit" ? parseFloatToDecimal(s["amount"],2)*1: 0);
            obj[keyId]["operations"].push(convert4acc(s));
        }
      });
    }
    else {
      obj = null;
    }

    return obj;
  }



  export function convert4acc(s) {
    let nItm = {};
    nItm["payChecksOnFutureId"] =  getStore("payChecksOnFutures_ids")?.[s["payChecksOnFutureId"]];
    nItm["comprobanteId"] = s["comprobanteId"];
    nItm["document"] = s["document"];
    nItm["date"] = s["date"];
    if(s["type"]==="credit"){
        nItm["debit"] = 0;
        nItm["credit"] = s["amount"];
        
    }
    else if(s["type"]==="debit"){
        nItm["credit"] = 0;
        nItm["debit"] = s["amount"];
    }
    return nItm;
  }



  
//  sorting episodes comparing inputs to save in cache
export const sortPayChecksOnFutures = (td, key) =>
callbackOnMemory(
	"sortPayChecksOnFutures",
	[td],
	() => {
        let order = true;
		let sortedA = td && td.sort((a, b) => {
			let A = a[key] * 1;
			let B = b[key] *1;
			if (A < B) {
				return order ? -1 : 1;
			}
			if (A > B) {
				return order ? 1 : -1;
			}
			return 0;
        });
		return sortedA;
	}
);


