import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, ObjectKeys, fetchEncryptStream, convertArray2Obj, convertArray2ObjGroupby, parseDateMMDDYYYY, parseFloatToDecimal } from "../../../stores/Utils";

import {SearchInput_Cmpt, CalendarModalPicker_Cmpt, SvgIcon_Cmpt } from "../../../components";

import { OpenModal } from "../../../components/DialogHRM";

import "./dashboard.css"
import { AddPayChecksOnFuture_Cmpt, PayChecksOnFutureItem_Cmpt } from "..";
import {  getall_payChecksOnFuture, search_payChecksOnFuture, get_filter_pay_future, loadStone } from "../services/server_fetching";
import { getall_provider } from "../../ProvidersCustomers/services/server_fetching";



const SearchInput = SearchInput_Cmpt();
const AddPayChecksOnFuture = AddPayChecksOnFuture_Cmpt();


const PayChecksOnFutureItem = PayChecksOnFutureItem_Cmpt();



const CalendarModalPicker = CalendarModalPicker_Cmpt();
const Svg = SvgIcon_Cmpt();


const frmNm = "birthcertificate"

const PayChecksOnFutureDashboard  = ({ data, elmId, updObs }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	const [loading, set_loading] = useState(false);




	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			set_obs(gen16CodeId())
			getall_provider(cb_result);
			//getall_payChecksOnFuture(cb_result);
			
		}
	});


	const openUpdateView =  () => {
	}


	



	
	const openAddView =  () => {
		loadStone();
		let data = {};
		data["zIndex"] = 450;
		data["height"] = "80vh";
		// data['observeResize']=true;
		data["props"] = { minHeight: "1vh", overlay: true };
		data["content"] = 
			<AddPayChecksOnFuture confirm={handle_getall_payChecksOnFuture} />
		OpenModal(data);
	}


	

	const handleChange =  (v) => {
		!loading && set_loading(true)
	}


	const handle_search_payChecksOnFuture =  (v) => {
		set_loading(true)
		search_payChecksOnFuture(cb_result)
	}


	const handle_getall_payChecksOnFuture =  (v) => {
		get_filter_pay_future(cb_result)
	}

	const cb_result = async (f, v) => {
		set_obs(gen16CodeId())
		set_loading(false);
	};


	

	const confirmCalendar = (v) => {
		 
	
		 
		if(v?.initDate && v?.lastDate ){
			let nv = {...v};
			nv.initDate = nv.initDate -100;
			nv.lastDate = nv.lastDate + 86380000;
			updStore("date_range_comps",nv);
		}
		get_filter_pay_future(cb_result);
	}
	

    const openCalendar = () => {
        let modal_data = {};
        modal_data["zIndex"] = 950;
        modal_data["height"] = "80vh";
        // data['observeResize']=true;
        modal_data["props"] = { minHeight: "1vh", overlay: true };
        modal_data["content"] = 
			<CalendarModalPicker 
				onConfirm={confirmCalendar} 
				init={getStore("date_range_comps")?.initDate}
				last={getStore("date_range_comps")?.lastDate}
				isRange={true}
			/>
        
        OpenModal(modal_data);
	}
	

	let dateRangeString = getStore("date_range_comps")?.initDate && parseDateMMDDYYYY(getStore("date_range_comps")?.initDate)  + "-" +parseDateMMDDYYYY(getStore("date_range_comps")?.lastDate);


	return (

	<div className="">
		<div className="accounts_header">
			<h2>Cheques a Futuro</h2>
			<div className="_dsplFlx ">
				<div className="group_title addBtn purple" onClick={openAddView}>
					Agregar Cheques
				</div>
				<div className="flexSpace"/>
				<div>
					<div className={"filter_date_btn _dsplFlx"} onClick={openCalendar}>
						<div className="lbl date">
								{dateRangeString || "Fecha"}
						</div>
						<Svg name={"calendar"} color={"#66666"}/>
					</div>
				</div>
			</div>
			<div className="_dsplFlx " style={{marginTop:14}}>
				<SearchInput
						placeholder={"Buscar ...."} 
						width={360}
						obs={set_obs}
						fetchData={handle_search_payChecksOnFuture}
						loading={loading}
						change={handleChange}
						callempty={handle_getall_payChecksOnFuture}
						keyFlds={"searchCFQry"}
					/>
				<div className="flexSpace"/>
				<div className="_dsplFlx ">
					<div className="total_comprobante_title">
						Qty de Cheques: 
					</div>
					<div className="total_comprobante gray">
						<span>
							{ObjectKeys(getStore("payChecksOnFutures_list"))?.length}
						</span>
					</div>
					<div className="lineV" />
					<div className="_dsplFlx acc_dtls" style={{margin: "10px 0 0 0"}}>
						<div className="_dsplFlx"  >
							<div className="name" style={{color: "var(--hrm_palette-item_coll_gray)"}}>
								{"Monto a Pagar:"} 
							</div>
							<div className="code" style={{marginLeft:8,color: "var(--hrm_palette-inverse-background-b)"}}>
								${SumBalance(getStore("payChecksOnFutures_list"))?.toFixed(2)} 
							</div>
						</div>
					</div>
					
				</div>
			</div>
		</div>

		
		<div className="_dsplFlx hdr_bar purple">
				<div className="icon tr_bar_5" ></div>
					<div className=" tr_bar_20" >
						Fecha
					</div>
					<div className=" tr_bar_10">
						No Cheque
					</div>
					<div className=" tr_bar_20">
						Documento
					</div>
					<div className=" tr_bar_30">
						Proveedor
					</div>
					<div className=" tr_bar_10">
						Importe
					</div>
				<div className="icon tr_bar_5" ></div>
			</div>
		
			<div className="payChecksOnFutures_container scollVh">
				{getStore("payChecksOnFutures_list") && ObjectKeys(getStore("payChecksOnFutures_list"))?.map(cid=>{
					return (
						<PayChecksOnFutureItem 
							data={getStore("payChecksOnFutures_list")[cid]} 
							elmId={cid} 
							key={cid}
							openUpdateView={openUpdateView}
							handleRefreshAll={handle_getall_payChecksOnFuture}
						/>
					)
				})}
			</div>
		
		</div>
	);
};

export default PayChecksOnFutureDashboard;


// PayFutureChecksHRGCollection_businessId_hcPiDPfZFS8ymqoh.json


const SumBalance = (obj) => {
	let balance = 0;
	obj && ObjectKeys(obj)?.map((flds, indE)=>{
		let amount = (obj[flds].amount*1);
		balance =  parseFloatToDecimal(balance,2)*1 + parseFloatToDecimal(amount,2)*1;
	});
	return  balance;
}  